import API from '../Api.json';
import Fetch from 'isomorphic-unfetch';


class Weatherwidget extends React.Component{

  state = {
    image: undefined
  }

  getImage = async() => {
    const key = ACCESS_KEY['Access Key'];
    console.log(key);
    fetch('https://api.unsplash.com/photos/?client_id='+key)
    .then(response => {
      if (response.status == 200) {
        return response
      } else {
        throw new Error();
      }
    })
    .then((responseData) => {
      const objectURL = URL.createObjectURL(responseData);
      this.setState({image:objectURL});
      console.log(responseData);
    })
  }

  render(){
    return(
      <div>
      <p>A test text piece</p>
        { this.getImage() }
      </div>
    );
  }
}

export default Weatherwidget;
